*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.content{
  height: 100vh;
  overflow-y: scroll;
}

.btn{
  margin: 20px 4px;;
}



body div.footer-div{ 
background: black;
color: white;
width: auto;
}

.navbar{
  background-color: rgb(243, 190, 121);
  display: flex;
  padding: 9px;
}


.user-and-button{
  display: flex;
}


.Register-page{
  background-color: rgb(114, 249, 249);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}



.username{
  background-color: rgb(246, 120, 120);
  color: rgb(8, 8, 8);
  font-weight: bold;
  border: 1px solid black;
  padding: 7px;
  border-radius: 9px;
}

/*transection css*/
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 8px 15px;
  padding:6px 6px;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
}
/*transection css*/